import React from 'react';
import { List } from 'react-bootstrap-icons';
import '../../../CSS/AboutUs.scss';
import { BsFillHandThumbsUpFill} from 'react-icons/bs';
import Modalex from './Modalex'



const AboutUs = () => {
  return (
    <div className="about-us-container relative top-16">
      <div className="left-section pl-10">
        <h2>About us</h2>
        <p>
        
        In 2010, the Igbo Cultural Association Northwest Ireland was established to promote cultural integration, community programs, and work towards peace, diversity, equality, and unity.<br/>
         Our primary objective is to create an inclusive and diverse environment where everyone can express themselves and receive support and respect. While we have faced challenges, we have embraced them as opportunities for growth and learning. Our current status is the result of our members' dedication, strong work ethic, and collaborative efforts.
         <br/>
        Our core values are committed to facilitating social interactions within your community, introducing you to new people, and enhancing your understanding of your surroundings. If you wish to learn more about our organization and the services we offer, please do not hesitate to contact us for a chat.

        </p>
        <p className="sign-up-button"><Modalex/></p>
      </div>
      <div className="right-section">
        <div className="card">
        <h5 className=" d-flex">
                <BsFillHandThumbsUpFill color='#900C3F'/>
                        <span className='text-light mx-2' >Aspects of Igbo Tradition</span>
 
              </h5>
              <ul>
          <li className="list list-1">Respect to Elders</li>
          <li className="list list-2">Special care to visitors</li>
          <li className = "list list-3">Breast Feeding {'->'} Mandatory </li>
          <li className="list list-4">Old peoples Support {'->'} Mandatory</li>
          <li className="list list-5">New born | mothers support {'->'} Mandatory</li>
          <li className= "list list-6">Children up-bringing {'->'} the community affair</li>

          </ul>
        </div>
      </div>
    </div>
  );
};

export default AboutUs;




